.status{
    max-width: 1062px;
    margin-left: auto;
    padding: 0 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    color: #0A243F;
    margin-bottom: 37px;
}
.about__sett__text{
    font-weight: bold;
    font-size: 18px;
    line-height: 28px;
    color: rgba(0, 0, 0, 0.8);
}
@media(max-width: 1450px){
    .status{
        max-width: 955px;
    }
}
@media(max-width: 1400px){
    .status{
        max-width: 975px;
    }
}
.status__left{
    display: flex;
    align-items: center;
}
.status__type{
    display: flex;
    align-items: center;
    margin-left: 20px;
    color: #0F47C0;
    img{
        margin-right: 2px;
    }
}
.status__right{
    display: flex;
    align-items: center;
    .form-switch{
        margin-left: 20px;
    }
}
.form-check-label, .form-check-input{
    cursor: pointer;
}
.status__left{
    .tooltips-wrap{
        .tooltips{
            font-family: 'Open Sans', sans-serif;   
            width: 395px;
            bottom: calc(100% + 0px);
            left: auto;
            transform: translateX(30px);
            right: 100%;
            font-size: 14px;
            font-weight: 400;
            padding: 5px 8px;
            letter-spacing: 0;
            a{
                text-decoration: underline;
                color: #9ABDFF;
                font-weight: 700;
            }
            a:hover{
                opacity: 0.9;
            }
        }
        .tooltips:before{
            left: auto;
            right: 15px;
        }
        .tooltips:after{
            content: "";
            left: 100%;
            position: absolute;
            height: 100%;
            width: 100px;
            display: block;
            top: 0;
        }
    }
    
}
@media(max-width: 1350px){
    .status__left{
        .tooltips-wrap{
            .tooltips{
                right: auto;
                left: 0;
                width: 250px;
                transform: translate(0);
            }
            .tooltips:before{
                left: 15px;
                right: auto;
            }
            .tooltips:after{
                display: none;
            }
        }
       
    }
}
@media(max-width: 480px){
    .status__left{
        .tooltips-wrap{
            .tooltips{
                right: 0;
                left: auto;
                top: 100%;
                bottom: auto;
                width: 250px;
            }
            .tooltips:before{
                right: 15px;
                left: auto;
                bottom: auto;
                top: -8px;
            }
        }
       
    }
}
.form-switch{
    padding-left: 3em;
    .form-check-input{
        width: 2.5em;
        margin-left: -3em;
        margin-top: 0.15em;
    }
    .form-check-input:checked{
        background-color: #0F47C0;
        border-color: #0F47C0;
    }
    .form-check-input{
        height: 1.4em;
    }
}

@media(max-width: 991px){
    .status{
        padding: 0;
    }
}

@media(max-width: 767px){
    .status{
        flex-direction: column;
        justify-content: flex-start;
